<script>
	import uniList from "../../components/uni-list/uni-list.vue";
	import uniListItem from "../../components/uni-list-item/uni-list-item.vue";
	import SwiperItem from "../../components/swiper/SwiperItem.vue";
	import common from '../../common/common';
	var _self;
	export default {
		components: {
			uniList,
			uniListItem,
			SwiperItem
		},
		data() {
			return {
				userinfo:{},
				quan:0,
				isShow:false,
				getUserObj:'',
				isFormal:1,
				num: 3,
				banner: [],
				swiperList:[],// H5 轮播图
				
			}
		},
		
		onLoad(){
			_self = this;
			// _self.checkProject();
			_self.getIndex()
		},
		onShow() {
			_self.userinfo = common.getstate();
			_self.getUserList()
			common.request({
				method:"GET",
				path: "login/getversion",
				success: res => {		
						_self.isFormal = res.data.Version;
				}
			})
		},
		
		methods: {
			// 
			toConcerns(){
				uni.navigateTo({
					url:'../news/concerns'
				})		
			},
			toComment(){
				uni.navigateTo({
					url:'../news/comment'
				})		
			},
			//结束
			getIndex() {
				common.request({
					path: "Development/getbanner",
					// method:'POST',
					data: {
						rows: 3,
						page: 1,
						sidx: "",
						sord: "desc",
					},
					success: res => {			
						res.data.rows.forEach((element,index) => {
							element.Images = common.PicURL + element.Images
						});
						_self.banner = res.data.rows
						
					}
				})
			},
			// checkProject(){
				 // common.request({
					// method:"GET",
				 // 	path: "login/getversion",
				 // 	success: res => {		
						
						// if(res.data.Version == 1){
						// 	uni.navigateTo({
						// 		url:'./show'
						// 	})				
						// }
				 // 	}
				 // })
			// },
			getUserList(){// 获取个人信息和积分余额	
				common.request({
					path: "Development/getmember",
					data: _self.userinfo.ID,
					success: res => {						
						_self.getUserObj = res.data.member;		
						_self.quan = res.data.rows								
					}
				})
			},
			chongzhibtn(){
				uni.showModal({
					title: '友情提示',
					content: '请前往福彩到家线下网点获取福豆',
					showCancel: false,
					confirmText:'查看网点',
					success(j) {
						uni.navigateTo({
							url:'../giveSelect/giveSelect?current=0&from=buy'
						})
					}
				})
			},
			signoutBtn(){				
				uni.showModal({
					title: '提示',
					content: '是否确认退出登录！',
					success: function(res) {
						if (res.confirm) {
							common.logout();
							uni.showToast({
								icon: 'none',
								title: '您已退出登录',	
							})							
							uni.navigateTo({
								url:'../login/userLogin'
							})							
						} 
					}
				})
			},
			callPhone(){  // 客服电话拨打
				_self.isShow = true				
			},
			handleClose(){  // 关闭客服
				_self.isShow = false
			},
			handleClone(){  // 复制微信号
				uni.setClipboardData({
					data: '1242444334',   // 要复制的文字
					success: function(res) {
						uni.getClipboardData({
							success: function(res) {
								uni.showToast({
									title: '复制成功'
								});
							}
						})
					}
				})
			},
			handleTanke(){   // 拨打电话
				uni.makePhoneCall({
					phoneNumber: '400-32324333'
				})
			},
			handleJifen(id){
				uni.navigateTo({
					url:'./myjifen?&id=' + id
				})
			},
			handleType(url){
				uni.navigateTo({
					url:url
				})
			},
			handleTiao(id){  // 状态的跳转			   
				uni.navigateTo({
					url: '../order/myOrder?id=' + id				
				})
			}
		}
	};
</script>
